html {
    min-height: 100%;
    overflow-y: scroll;
}

body {
    height: 100vh;
    margin: 15% 0;
    background-color: #fdfcf7;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
}
.setPadding {
    padding: 10% 10% 20%;
}

/* navbar */
.navbar-brand img {
    border-radius: 50%;
    margin-right: 10px;
}
.navbar-toggler {
    background-color: rgb(131, 8, 8);
    padding: 1px 6px;
}
.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:hover {
    color: #f5c5c7;
}
/* override bootstrap navbar colors 
override background image gradient w/ flat color */
/*.navbar,
.navbar-default {
    background-image: linear-gradient(to bottom,#2c4053 0,#2c4053 100%) !important; 
} */
.colorNav {
    background-color: rgb(131, 8, 8);
}
.colorNav .navbar-brand {
    color: white;
}

.navbar-light .navbar-toggler {
    color: rgba(0,0,0,.5);
    border-color: rgba(0,0,0,.1);
}

.navbar-light .navbar-nav .nav-link {
    color: white;
}

.navbar-nav {
    align-items: flex-end;
}

.navContainer {
    flex-direction: column;
    justify-content: space-evenly;
    min-height: 300px;
}
.navContainer a {
    color: white;
    padding-right: 5px;
}

/* Recruitment  */
.recruitment {
    color: #453a3c;
    background-color: #F5F5F5;
    margin-top: 5px;
    padding: 10px 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0.5em 0;
}

.recruitment a {
    text-decoration-line: underline;
    text-decoration-color: #453a3c;
    color: #453a3c;
    font-weight: 700;
}

