/* Carousel */
.carousel-item img {
    padding-top: 36vh;
}
.carousel-caption {
    top: 1em;
    color: black;
    padding-top: 0;
}
.carousel-control-next,
.carousel-control-prev {
    bottom: -36vh;
}

/* Button Browse  */
.btn-primary {
    background-color: #830808;
    border-color: #830808;
}
.btn-primary.focus,
.btn-primary:focus,
.btn-primary:hover,
.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
    background-color: #47AEA0;
    border-color: #47AEA0;
}

