/* carousel indicators */

.carousel-indicators li {
    background-color: white;
    opacity: 1;
    height: 7px;
}
.carousel-indicators .active {
    background-color: #47AEA0;
}

.getStarted,
.moreProducts {
    background-color: #47AEA0;
    border-color: #47AEA0;
    font-size: 1.3em;
}

.moreProducts {
    width: 100%;
}

.getStarted {
    width: 70%;
}

.centerImage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 15%;
}

.extraPadding {
    padding-bottom: 25%;
}

.centerImage p {
    width: 70%;
    text-align: center;
}
.roundImage {
    width: 200px;
    height: 200px;
}

.imageXs {
    width: 75px;
    height: 75px;
}

.extraBorder {
    border-left: 1px solid #212529;
    border-right: 1px solid #212529;
}

.youTube {
    width: 100%;
    height: auto;
    padding: 10% 0;
}

.centerText {
    text-align: center;
    padding-bottom: 10%;
}

.locationMap {
    text-align: center;
    padding-bottom: 10%;
}
.myMap.col {
    border: 1px solid transparent;
    border-radius: 0.3em;
    box-shadow: 4px 6px 10px #aaaaaa;
    width: 100%;
    height: 300px;
    padding: 0;
}

.myMap Iframe {
    border-radius: 0.3em;
    border: 1px solid transparent;
}

.setFlex {
    display: flex;
    flex-direction: column;
}
.setRow {
    display: flex;
    flex-direction: column;
}

.setCol {
    display: flex;
    flex-direction: row;
    padding-right: 0;
    padding-left: 5px;
    padding-bottom: 2em;
    align-items: center;
    text-align: left;
}

.setCol p {
    font-size: 0.8em;
    margin-bottom: 0;
}

.setThumnail {
    height: 2em;
    padding-right: 10px;
}

.footer {
    font-size: 0.8em;
    font-weight: 100;
    text-align: center;
    color: white;
    background-color: #830808;
    padding: 10px;
    width: 100%;
    margin-left: 0;
}
